  .nav-link:hover {
    box-shadow: transparent 0px 3px 0px 0px inset;
  }
  .nav-link.w--current {
    box-shadow: transparent 0px 1px 0px 0px inset;
  }
  .nav-link.menu {
    padding: 22px 17px;
    color: black;
    font-size: 30px;
  }
  .nav-link.menu.w--open {
    background-color: #1a81ff;
    color: rgba(255, 255, 255, 0.8);
  }
  .nav-link.menu.w--open, .nav-link.menu.w--open:hover, .nav-link.menu.w--open:focus {
    box-shadow: none;
  }
  .nav-menu {
    padding-top: 13px;
    padding-bottom: 13px;
    background-color: #1a81ff;
  }
  .main-feature-group {
    margin-top: 0px;
  }
  .main-image {
    margin-top: 49px;
  }
}

