@media (max-width: 991px)
  .button.in-pricing
    font-size: 14px
    line-height: 18px

  .nav-link
    padding: 18px 0px
    &, &:hover, &:focus
      color: #fff
    &:hover
      box-shadow: transparent 0px 3px 0px 0px inset

    &.w--current
      box-shadow: transparent 0px 1px 0px 0px inset

    &.menu
      padding: 22px 17px
      color: black
      font-size: 30px

      &.w--open
        background-color: $blue-bright-adjusted
        color: rgba(#fff, 0.8)
        &, &:hover, &:focus
          box-shadow: none

  .nav-menu
    padding-top: 13px
    padding-bottom: 13px
    background-color: $blue-bright-adjusted

  .main-feature-group
    margin-top: 0px

  .main-image
    margin-top: 49px

